@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap');
// -------------- THEME SWITCHER -------------- //
@mixin dark-appearance {
  filter: invert(1);
  img {
    filter: invert(1);

    &.ioda { filter: invert(0); }
  }
}

body[a="dark"] { @include dark-appearance; }


@media (prefers-color-scheme: dark) {
  body[a="auto"] { @include dark-appearance; }
}
// -------------------------------------------- //

// bg color is also needed in html in order to
// block body's background propagation
// see: https://stackoverflow.com/a/61265706
html, body { background: white; }

html { height: 100%; }

body {
  color: black;
  font-family: 'EB Garamond', serif;
  font-size: 16px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
}

.post-meta { text-align: right; }

h2, h3, h4, h5, h6 { margin-top: 3rem; }

hr { margin: 2rem 0; }

p { margin: 1rem 0; }

li { margin: 0.4rem 0; }

*:target { background: yellow; }

.w {
  max-width: 640px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

hr {
  text-align: center;
  border: 0;
}

table { width: 100%; }

table, th, td {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem;
}

code {
  color: white;
  background: black;
}

div.highlighter-rouge code {
  display: block;
  overflow-x: auto;
  white-space: pre-wrap;
  padding: 1rem;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem;

  p { margin: 0; }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

/* Style for the main navigation container */
.main-navigation {
  background-color: #f8f5ee; /* Light background for the nav bar */
  padding: 10px 0;
  margin-top: -15px; /* Add or change this line */
}

/* Style for the top-level unordered list */
.main-navigation > ul {
  list-style: none; /* Remove bullet points */
  padding: 0;
  margin: 0;
  display: flex; /* Make list items horizontal */
  justify-content: center; /* Center the menu items */
  flex-wrap: wrap; /* Allow items to wrap to the next line on smaller screens */
}

/* Style for top-level list items */
.main-navigation > ul > li {
  margin: 0 25px; /* Spacing between menu items */
  position: relative; /* Needed for dropdowns if you implement them */
}

/* Style for menu links */
.main-navigation a {
  text-decoration: none; /* Remove underline */
  color: #333;
  font-weight: bold;
  padding: 8px 12px;
  display: block; /* Make the whole link area clickable */
  transition: color 0.3s ease, background-color 0.3s ease;
}

.main-navigation a:hover {
  color: #008080; /* Hover color */
  background-color: #e9e9e9; /* Light background on hover */
  border-radius: 4px;
}

/* Basic styling for nested menus (if you have them) */
.main-navigation ul ul { /* Targets nested <ul> */
  display: none; /* Hide sub-menus by default */
  position: absolute; /* Position relative to parent li */
  background-color: #fff;
  box-shadow: 0 0px 0px rgba(0,0,0,0);
  min-width: 160px;
  z-index: 1000;
  padding: 10px 0;
  border-radius: 0px;
}

.main-navigation li:hover > ul {
  display: block; /* Show sub-menu on hover */
}

.main-navigation ul ul li {
  margin: 0; /* Remove horizontal margin for sub-menu items */
}

.main-navigation ul ul a {
  padding: 8px 15px;
  color: #555;
}

.main-navigation ul ul a:hover {
  background-color: #f0f0f0;
  color: #007bff;
}